
import { SortBy } from 'types/SortBy';
import { defineComponent, PropType } from 'vue';

export default defineComponent({
  name: 'SortSidebar',
  props: {
    value: {
      required: true,
      type: String as PropType<SortBy>,
    },
  },
  emits: {
    ['update:value']: (v: SortBy) => {
      return v === 'date' || v === 'value';
    },
  },
});
